@media (hover: hover) {
     ::-webkit-scrollbar {
        width: 3px;
        height: 3px;
        background: map-get($colors, 'light');
    }
     ::-webkit-scrollbar-thumb {
        background: map-get($colors, 'dark');
    }
}

::selection {
    background-color: map-get($colors, 'dark');
    color: map-get($colors, 'light');
}